import Vue from 'vue'
import Board from './Board'
import VueRes from 'vue-resource'

Vue.use(VueRes);
/* eslint-disable no-new */
new Vue({
  el: 'body',
  data: {
  	selected_add: 1,
  	board:[],
  	enter: false,
  	who:'',
  },
  components: { Board },
  methods: {
  	validate(){
  		
  		this.enter = this.who==='zxr-gloria-me'? true: false;
  		this.who = ''; 

  	},
  	add(e){
  		//获取棋盘信息
  		this.$broadcast('Array_81');
	 	let data = {
	 		level: parseInt(this.selected_add),
 			board: this.board,  
	 	};
	 	
	 	//发送添加请求
	 	this.$http.post('/admin/add_board',data).then((res)=>{
	 		let resText = res.text();
	 		//清空棋盘
	 		this.$broadcast('emptyBoard');
	 		alert(resText);

	 	},(res)=>{
	 		alert('发送失败~~');
	 	})

	 	console.log(data);
  	}
  }
})



