<template>
  <div id="board">
    <table class="board">
      <tr v-for="(index , row) in board" track-by="$index">
        <td v-for="(index2 , item) in row" @mouseout="mouseout()" @click="click(index,index2)" track-by="$index">{{item}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
let emptyBoard = generateArray();
//输入状态
let inputState = {
  bool: false,
  row:-1,
  col:-1,
};
//生成棋盘数组
function generateArray(char,position={row:-1,col:-1}){
  let arr = [];
  for(let i=0;i<9;i++){
  let rowArray = [];
    for(let j=0;j<9;j++){
      let c ='';
      if(i===position.row&&j===position.col) c=char;
      rowArray.push(c);
    }
    arr.push(rowArray);
  }
  return arr;  
}
//棋盘数组转换为Array[81]
function Array_81(arr){

  let result = [...arr];

  for(let i=0;i<9;i++){
    for(let j=0;j<9;j++){
      let item = result[i][j];
      if(item=='') result[i][j] = '%';
      
    }
    result[i] = result[i].join('');
  }
  result = result.join('');
  return result;
}
///keyup事件控制器
function Keyup(e){
  if(!inputState.bool) return;
  let row = inputState.row;
  let col = inputState.col;
  let key = e.keyCode===46? '' : e.keyCode-48;
  
  this.board[row].splice(col,1,key);
}
export default {
  data(){
    return {
      board: emptyBoard,
    }
  },
  computed: {
  },
  methods:{
    click(row,col){
      //开启输入状态，记录位置
      inputState.bool = true;
      inputState.row = row;
      inputState.col = col;

      //添加表格样式 .focus
      let oTd = document.querySelector('.board').querySelectorAll('tr')[row].querySelectorAll('td')[col];

      oTd.classList.add('focus');

    },
    mouseout(){
      //删除样式
      let row = inputState.row;
      let col = inputState.col;

      let oTd = document.querySelector('.board').querySelectorAll('tr')[row].querySelectorAll('td')[col];

      oTd.classList.remove('focus');
      //关闭输入状态，还原位置
      inputState.bool = false;
      inputState.row = -1;
      inputState.col = -1;
      

    }
  },
  events: {
    'Array_81': function(){
      this.Board = Array_81(this.board);
    },
    'emptyBoard': function(){
      this.board = generateArray();
    }
  },
  ready(){
    window.onkeyup = Keyup.bind(this);
  },
  props: ['Board'],
  
}
</script>

<style lang="scss">
body,html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color:rgb(153, 153, 153);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI","微软雅黑", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
div,h1,h2,h3,h4,ul,li,table,tt,td,th,tbody,p{
  padding: 0;
  margin: 0;
  list-style: none;
  box-sizing: border-box;
}
#board {
  float: left;
  padding: 10px;
}
.board {
  display: inline-table;
  width: 540px;
  height: 540px;
  border-collapse: collapse;
  td {
    width: 60px;
    height: 60px;
    border: 3px solid black;
    text-align: center;
    color: white;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    &:hover {
      background-color: green;
      transform: scale(1.3);
    }
    &.focus {
      background-color: red;
    }
  }

}
</style>



